@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;1,100&display=swap");

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
body {
  background: rgb(174, 219, 238);
  background: -o-radial-gradient(
    circle,
    rgba(174, 219, 238, 1) 0%,
    rgb(128, 145, 221) 100%
  );
  background: radial-gradient(
    circle,
    rgba(174, 219, 238, 1) 0%,
    rgb(128, 145, 221) 100%
  );
}

.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding:5rem 0;
  height: 100vh;
  background: transparent;
}
.wrapper::before {
  content: "";
  position: absolute;
  left: 45%;
  width: 800px;
  height: 800px;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgb(255, 251, 0)),
    color-stop(rgb(255, 153, 0)),
    color-stop(rgb(255, 38, 0)),
    to(rgb(255, 0, 0))
  );
  background: -o-linear-gradient(
    rgb(255, 251, 0),
    rgb(255, 153, 0),
    rgb(255, 38, 0),
    rgb(255, 0, 0)
  );
  background: linear-gradient(
    rgb(255, 251, 0),
    rgb(255, 153, 0),
    rgb(255, 38, 0),
    rgb(255, 0, 0)
  );
  border-radius: 50%;
}

.tasks-card {
  position: relative;
  width: 100vw;
  height: auto;
  left: 0;
  right: 0;
  border-radius: 5px;
  padding:2rem;
}
.tasks-card .header {
  width: 100%;
  height: 2rem;
  margin: 2rem 1rem;
  text-align: center;
  font-size: 3rem;
  border-radius: 5px 5px 0 0;
}
.tasks-card .error {
  color: red;
  font-size: 1rem;
  text-align: center;
  margin-top: 0.1rem;
}

.tasks-card .tasks-form {
  width: 100%;
  height: auto;
  margin: 1rem 0;
  text-align: center;
  font-size: 2rem;
  border-radius: 5px 5px 0 0;
}

.tasks-card .tasks-form form input {
  outline: none;
  border: none;
  width: 100%;
  border-radius: 5px;
  padding: 0 0.3rem;
}
.tasks-card .tasks-form form .tasks-btn {
  font-size: 0.8rem;
  font-weight: bold;
  width: 100%;
  margin: 0.4rem 0;
  padding: 0 0.3rem;
  color: #111;
  outline: none;
  border: none;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
}
.tasks-card .tasks-form form .tasks-btn:hover {
  border: 1px solid rgb(255, 255, 255);
  background-color: rgba(17, 17, 17, 0.6);
  color: #fff;
}

.task-list {
  border: 0.1rem solid rgba(0, 0, 0, 0.4);
  padding: 0.3rem;
  width: 100%;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
.task-list span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.task-list .task-card {
  position: relative;
  width: 100%;
  height: auto;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.4);

  margin-top: 0.1rem;
}
.task-list .task-card .task-card-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
  height: auto;
  padding: 0.3rem;
}
.task-list .task-card .task-card-body h4 {
  color: #fff;
}
.task-list .task-card .task-card-body i {
  cursor: pointer;
  color: white;
}

.task-list .task-card .task-card-body i:hover {
  color: red;
}

@media (max-width: 991px) and (min-width: 769px) {
  .wrapper::before {
    top: 15%;
    left: 10%;
    width: 700px;
    height: 700px;
  }

  .tasks-card .header {
    font-size: 3.5rem;
    height: 4rem;
    margin: auto;
  }

  .tasks-card .tasks-form {
    margin: 0;
    font-size: 2rem;
  }

  .tasks-card .tasks-form form .tasks-btn {
    border: 1px solid rgb(255, 255, 255);
    background-color: rgba(17, 17, 17, 0.6);
    color: #fff;
  }
}
@media (max-width: 768px) {
  .wrapper::before {
    top: 10%;
    left: 3.2%;
    width: 345px;
    height: 345px;
  }
  .tasks-card .header {
    background: rgba(255, 255, 255, 0.5);
    font-size: 1.7rem;
    height: 3.2rem;
    margin: auto;
  }

  .tasks-card .tasks-form {
    margin: 0;
    font-size: 2rem;
  }
}
